<template>
  <div id="app">
    <header class="header">
      <div>{{ blogName }}的个人博客</div>
      <section class="amount-sum">
        <span>文章总数：{{ articleAmount }}</span>
        <span>点击总数：{{ clickAmount }}</span>
      </section>
    </header>
    <section class="main">
      <aside class="side">
        <section class="list-guide">
          <div class="banner">导航</div>
          <router-link :to="{name: 'ArticleByAuthor', params: {authorID: authorID}}">
            <div ref="home">首页</div>
          </router-link>
          <template v-if="login === true">
            <router-link :to="{name: 'ArticleAdding', params: {authorID: authorID}}">
              <div>发表新博文</div>
            </router-link>
            <router-link :to="{name: 'ColumnByAuthor', params: {authorID: authorID}}">
              <div>栏目管理</div>
            </router-link>
            <div @click="logOut">
              <a>退出</a>
            </div>
          </template>
          <router-link v-else :to="{name: 'Login'}">
            <div ref="login">登录</div>
          </router-link>
        </section>
        <section class="list-column">
          <div class="banner">栏目列表</div>
          <div v-if="!columns" style="margin: 10px 10px">当前该作者没有栏目</div>
          <div v-for="list in columns" v-bind:key="list.columnID">
            <router-link :to="{name: 'ArticleByColumn', params: {columnID: list.columnID}}" :login-flag="login">
              <div>{{ list.columnName }} ({{ list.articleAmount }})</div>
            </router-link>
          </div>
        </section>
      </aside>
      <section class="router-view">
        <!--用router-view渲染视图-->
        <router-view :login-flag="login"></router-view>
      </section>
    </section>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      blogName: '',
      articleAmount: 0,
      clickAmount: 0,
      login: false,
      authorID: 1,
      columns: []
    }
  },
  methods: {
    logOut () {
      const url = 'http://lipohong.fun:2333/api/user'
      this.$http.patch(url, {}) // 退出
        .then(() => {
        })
      this.login = false
      this.authorID = 1 // 默认是用户ID 1 的博客内容
      this.$cookies.remove('authorID')
      this.init()
    },
    checkHeader (authorID) {
      let url = 'http://lipohong.fun:2333/api/userID/' + authorID
      this.$http.get(url, {})
        .then((res) => {
          this.blogName = res.body.data.authorNickName
          this.articleAmount = res.body.data.articleAmount
          this.clickAmount = res.body.data.clickAmount
        })
    },
    checkColumn (authorID) {
      let url = 'http://lipohong.fun:2333/api/column/authorID/' + authorID
      this.$http.get(url, {})
        .then((res) => {
          if (res.body.data == null) {
            this.columns = null
            this.$forceUpdate() // 强制更新
            return
          }
          let columns = res.body.data.columns
          // 查每个栏目下的文章数目
          columns.forEach((column) => {
            let url = 'http://lipohong.fun:2333/api/columnID/' + column.columnID
            this.$http.get(url, {})
              .then((res) => {
                column.articleAmount = res.body.data.articleAmount
                this.$forceUpdate() // 强制更新，显示文章数目
              })
          })
          this.columns = columns
        })
    },
    init () {
      const authorID = this.$cookies.get('authorID')
      if (authorID) {
        this.authorID = authorID
        this.login = true
        this.checkHeader(authorID) // 请求header相关数据
        this.checkColumn(authorID) // 请求栏目相关数据
      } else {
        this.checkHeader(this.authorID)
        this.checkColumn(this.authorID)
      }
      // DOM更新后触发事件
      this.$nextTick(() => {
        this.$refs.home.click() // 模拟点击首页，显示全部文章
      })
    }
  },
  mounted () {
    this.init()
    // 监听登录事件
    this.common.$on('login', () => {
      this.init()
    })
    // 监听请求登录事件
    this.common.$on('loginRequest', () => {
      this.logOut()
      this.$nextTick(() => {
        this.$refs.login.click()
      })
    })
    // 监听要求更新事件
    this.common.$on('update', () => {
      const authorID = this.$cookies.get('authorID')
      if (authorID) {
        // 已登录
        this.checkHeader(authorID) // 请求header相关数据
        this.checkColumn(authorID) // 请求栏目相关数据
      } else {
        // 未登录
        this.checkHeader(this.authorID)
        this.checkColumn(this.authorID)
      }
    })
    // 监听要求初始化事件
    this.common.$on('init', () => {
      this.init()
    })
  }
}
</script>

<style>
  body{
    margin: 0;
    padding: 0;
    background: #cffcff;
  }
  a {
    text-decoration: none;
    cursor: pointer;
    color: #000;
  }
  a:hover {
    color: #ff0000;
  }
  #app {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .header {
    position: absolute;
    width: 1120px;
    height: 80px;
    top: 20px;
    left: 50%;
    margin-left: -560px;
  }
  .header div {
    margin-top: 15px;
    margin-left: 20px;
    font-size: 28px;
    font-weight: bolder;
  }
  .amount-sum {
    margin-top: 10px;
    margin-right: 20px;
    text-align: right;
  }
  .main {
    position: absolute;
    width: 1160px;
    left: 50%;
    top: 120px;
    margin-left: -580px;
  }
  .side {
    position: absolute;
    left: 20px;
    width: 300px;
    text-align: center;
  }
  .side div {
    height: 30px;
    line-height: 30px;
    border-bottom: 1px solid #CCC;
  }
  .banner {
    font-weight: bolder;
    background: #ff5000;
  }
  .list-guide {
    border: 1px solid #CCC;
    background: #FFF;
  }
  .list-column {
    margin-top: 20px;
    border: 1px solid #CCC;
    background: #FFF;
  }
  .router-view {
    position: absolute;
    width: 800px;
    min-height: 100px;
    left: 340px;
    border: 1px solid #CCC;
    background: #FFF;
    margin-bottom: 50px;
  }
  .router-banner {
    font-weight: bolder;
    background: #ff5000;
    height: 30px;
    line-height: 30px;
    text-align: center;
  }
  button {
    padding: 10px 20px;
    font-size: 15px;
    border: 1px solid #eee;
    border-radius: 5px;
    cursor: pointer;
  }
  .button-confirm {
    background: #00bfff;
  }
  .button-confirm:hover {
    background: #0093c9;
  }
  .button-cancel {
    background: #ff0000;
  }
  .button-cancel:hover {
    background: #ca0000;
  }
</style>



