
// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'

// 引入富文本编辑器quill
import VueQuillEditor from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

// 引入创建的页面
import articleByAuthor from '../src/components/ArticleByAuthor.vue'
import articleByColumn from '../src/components/ArticleByColumn.vue'
import articleReading from '../src/components/ArticleReading.vue'
import articleAdding from '../src/components/ArticleAdding'
import login from '../src/components/Login.vue'
import columnByAuthor from '../src/components/ColumnByAuthor'
import articleUpdating from '../src/components/ArticleUpdating'

// 引入通讯模块
import VueResource from 'vue-resource'

// 引入cookies模块
import VueCookies from 'vue-cookies'

// 使用路由实例插件
Vue.use(VueRouter)
// 使用通讯实例插件
Vue.use(VueResource)

/*
Vue.http.options.headers = {
  'Credentials': 'true'
}
*/

// 使用vue cookies 插件
Vue.use(VueCookies)
// 使用 quill 富文本插件
Vue.use(VueQuillEditor)

const router = new VueRouter({
  mode: 'history',
  routes: [
    // 将页面组件与path制定的路由关联
    {name: 'ArticleByAuthor', path: '/articleByAuthor/:authorID', component: articleByAuthor},
    {name: 'ArticleByColumn', path: '/articleByColumn/:columnID', component: articleByColumn},
    {name: 'ArticleReading', path: '/article/:articleID', component: articleReading},
    {name: 'ArticleAdding', path: '/articleAdding/:authorID', component: articleAdding},
    {name: 'ArticleUpdating', path: '/articleUpdating/:articleID/', component: articleUpdating},
    {name: 'Login', path: '/login', component: login},
    {name: 'ColumnByAuthor', path: '/columnByAuthor/:authorID', component: columnByAuthor}
  ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  // 将路由实例添加到Vue实例中
  router,
  beforeCreate () {
    Vue.prototype.common = this
  },
  render: h => h(App)
})



