<template>
  <div>
    <div class="router-banner">栏目博文列表</div>
    <div v-if="!articles" style="margin: 10px 10px">当前该栏目下没有博文</div>
    <div class="article-list" v-for="list in articles" :key="list.articleTitle">
      <router-link :to="{name: 'ArticleReading', params: {articleID: list.articleID}}">
        <h3>{{ list.articleTitle}}</h3>
      </router-link>
        <p>{{ list.articleContent }}...</p>
        <span>最后更新：{{ list.articleTime }}</span>
        <span>|</span>
        <span>阅读量：{{ list.articleClick }}</span>
      <template v-if="loginFlag === true">
        <span>|</span>
        <router-link :to="{name: 'ArticleUpdating', params: {articleID: list.articleID}}">
          <span>编辑</span>
        </router-link>
        <span>|</span>
        <a style="margin-left: 20px" @click="deleteArticle(list.articleID)">删除</a>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      articles: []
    }
  },
  props: {
    loginFlag: Boolean
  },
  methods: {
    dateFormat (dateOrigin) {
      const date = new Date(dateOrigin)
      const year = date.getFullYear()
      let month = date.getMonth() + 1 // 月份从0开始计算
      month = month >= 10 ? month : '0' + month
      let day = date.getDate()
      day = day >= 10 ? day : '0' + day
      let hour = date.getHours()
      hour = hour >= 10 ? hour : '0' + hour
      let minute = date.getMinutes()
      minute = minute >= 10 ? minute : '0' + minute
      let second = date.getSeconds()
      second = second >= 10 ? second : '0' + second
      return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
    },
    getArticleByColumn () {
      const url = 'http://lipohong.fun:2333/api/article/columnID/' + this.$route.params.columnID
      // 请求栏目相关数据
      this.$http.get(url, {params: {isDesc: true}})
        .then((res) => {
          if (res.body.data == null) {
            this.articles = null
            return
          }
          this.articles = res.body.data.articles
          if (this.articles == null || this.articles.length === 0) {
            return // 没有文章
          }
          // 格式化日期
          for (let index in this.articles) {
            const date = new Date(this.articles[index].articleTime)
            this.articles[index].articleTime = this.dateFormat(date)
          }
        })
    },
    deleteArticle (articleID) {
      const response = confirm('确定删除该博文吗')
      if (response) {
        const url = 'http://lipohong.fun:2333/api/articleID/' + articleID
        this.$http.delete(url, {'credentials': true})
          .then((res) => {
            const responseData = res.body
            if (responseData.code === 1341) {
              alert('博文删除成功')
              this.getArticleByColumn()
            } else if (responseData.code === 1006) {
              alert('请先登录')
              this.common.$emit('loginRequest', {})
            } else {
              alert('博文删除失败, ' + responseData.message)
            }
          })
      }
    }
  },
  mounted () {
    this.getArticleByColumn()
  },
  watch: {
    '$route.params': function () {
      this.getArticleByColumn()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  p {
    color: #808080;
  }
  h3, p {
    margin-left: 20px;
  }
  .article-list {
    position: relative;
    padding-bottom: 20px;
    padding-left: 10px;
    border-bottom: 1px solid #CCC;
  }
  .article-list span {
    margin-left: 20px;
  }
</style>



