<template>
  <div>
    <div class="router-banner">{{ message }}</div>
    <form class="form">
      <div>
        <span>用户名</span>
        <input type="text" ref="userName"/></div>
      <div>
        <span>密码</span>
        <input type="password" ref="password" autocomplete="password"/>
      </div>
    </form>
    <div class="button-box">
      <button @click="submit" class="button-confirm">登录</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: '登录'
    }
  },
  methods: {
    submit () {
      const userName = this.$refs.userName.value
      if (!userName) {
        this.message = '请输入用户名'
        return
      }
      const password = this.$refs.password.value
      if (!password) {
        this.message = '请输入密码'
        return
      }
      const data = {'password': password}
      const url = 'http://lipohong.fun:2333/api/userName/' + userName
      // 发送登录数据
      this.$http.patch(url, data, {'emulateJSON': true, 'credentials': true})
        .then((res) => {
          const data = res.body
          // 格式化日期
          if (data.code === 1001) {
            this.message = '登录成功'
            this.$cookies.set('authorID', data.data.authorID) // 将id 保存在cookies中
            this.common.$emit('login')
          } else {
            this.message = '登录失败, ' + data.message
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .form {
    position: relative;
    width: 300px;
    margin-top: 30px;
    left: 250px;
    font-size: 18px;
  }
  .form input {
    width: 200px;
    font-size: 16px;
  }
  .form div {
    margin-top: 10px;
  }
  .form div input {
    float: right;
    height: 20px;
  }
  .button-box {
    position: relative;
    left: 50%;
    margin-left: -40px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
</style>



