<template>
  <div>
    <div class="router-banner">正文</div>
    <div class="article">
      <article class="ql-container ql-snow">
        <header>
          <div class="title">{{ article.articleTitle}}</div>
          <span>最后更新日期：{{ article.articleTime }}</span>
          <span>|</span>
          <span>阅读量：{{ article.articleClick }}</span>
          <template v-if="loginFlag === true">
            <span>|</span>
            <router-link :to="{name: 'ArticleUpdating'}">
              <a a style="margin-left: 20px">编辑</a>
            </router-link>
            <span>|</span>
            <a style="margin-left: 20px" @click="deleteArticle(articleID)">删除</a>
          </template>
        </header>
        <hr>
        <div class="ql-editor" v-html="article.articleContent"></div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      article: [],
      articleID: this.$route.params.articleID
    }
  },
  props: {
    loginFlag: Boolean
  },
  methods: {
    dateFormat (dateOrigin) {
      const date = new Date(dateOrigin)
      const year = date.getFullYear()
      let month = date.getMonth() + 1 // 月份从0开始计算
      month = month >= 10 ? month : '0' + month
      let day = date.getDate()
      day = day >= 10 ? day : '0' + day
      let hour = date.getHours()
      hour = hour >= 10 ? hour : '0' + hour
      let minute = date.getMinutes()
      minute = minute >= 10 ? minute : '0' + minute
      let second = date.getSeconds()
      second = second >= 10 ? second : '0' + second
      return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
    },
    deleteArticle (articleID) {
      const response = confirm('确定删除该博文吗')
      if (response) {
        const url = 'http://lipohong.fun:2333/api/articleID/' + articleID
        this.$http.delete(url, {'credentials': true})
          .then((res) => {
            const responseData = res.body
            if (responseData.code === 1341) {
              alert('博文删除成功')
              this.common.$emit('init')
            } else if (responseData.code === 1006) {
              alert('请先登录')
              this.common.$emit('loginRequest', {})
            } else {
              alert('博文删除失败, ' + responseData.message)
            }
          })
      }
    }
  },
  mounted () {
    const url = 'http://lipohong.fun:2333/api/articleID/' + this.$route.params.articleID
    // 请求文章相关数据
    this.$http.get(url, {})
      .then((res) => {
        this.article = res.body.data
        // 格式化日期
        this.article.articleTime = this.dateFormat(this.article.articleTime)
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  header {
    text-align: center;
  }
  header span {
    margin-left: 20px;
    color: #666;
    font-size: 15px;
  }
  hr {
    width: 780px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-top: 1px solid #ccc;
    border-bottom: none;
  }
  a {
    color: #666;
    font-size: 15px;
    text-decoration: none;
    cursor: pointer;
  }
  a:hover {
    color: #ff0000;
  }
  .ql-container {
    border: none;
  }
  .title {
    font-size: 30px;
    font-weight: bold;
    margin: 15px 0;
  }
  .article {
  }
</style>



