<template>
  <div>
    <div class="router-banner">{{ message }}</div>
    <article>
      <header>
        <div>
          <input ref="title" placeholder="请输入标题"/>
          <select ref="columnList">
            <option>选择栏目</option>
            <option v-for="list in columns" v-bind:key="list.columnID" :value="list.columnID">{{ list.columnName }}</option>
          </select>
          <button @click="updateArticle" class="button-confirm">更新</button>
        </div>
      </header>
      <div>
        <div class="quill-box">
          <template>
            <quill-editor v-model="content"
                          :options="editorOption"
                          ref="content">
            </quill-editor>
          </template>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
import { quillEditor, Quill } from 'vue-quill-editor'
const toolbarOptions = [
  ['bold', 'italic', 'underline', 'strike'],
  ['blockquote', 'code-block'],
  [{'list': 'ordered'}, {'list': 'bullet'}],
  [{'indent': '-1'}, {'indent': '+1'}],
  [{'color': []}, {'background': []}],
  [{'align': []}],
  ['link', 'image'],
  ['clean'],
  [{'size': ['small', false, 'large', 'huge']}],
  [{'header': [1, 2, 3, 4, 5, 6, false]}],
  [{'font': []}]
]
export default {
  data () {
    return {
      message: '编辑博文',
      columns: [],
      content: '',
      editorOption: {
        placeholder: '请输入博文内容',
        modules: {
          toolbar: {
            container: toolbarOptions,
            handlers: {
              'image': function (value) {
                if (value) {
                  const range = this.quill.getSelection()
                  const value = prompt('请输入图片网址')
                  this.quill.insertEmbed(range.index, 'image', value, Quill.sources.USER)
                } else {
                  this.quill.format('image', false)
                }
              }
            }
          }
        }
      }
    }
  },
  components: {
    quillEditor
  },
  methods: {
    getColumn (authorID, columnID) {
      let url = 'http://lipohong.fun:2333/api/column/authorID/' + authorID
      this.$http.get(url, {})
        .then((res) => {
          this.columns = res.body.data.columns
          const selectOption = this.$refs.columnList
          this.$nextTick(() => {
            for (let i = 0; selectOption.options.length; i++) {
              if (selectOption.options[i].value === columnID.toString()) {
                selectOption.options[i].selected = 'selected'
              }
            }
          })
        })
    },
    getArticle () {
      const url = 'http://lipohong.fun:2333/api/articleID/' + this.$route.params.articleID
      // 请求文章相关数据
      this.$http.get(url, {})
        .then((res) => {
          const article = res.body.data
          const columnID = article.columnID
          const authorID = article.authorID
          this.$refs.title.value = article.articleTitle
          this.content = article.articleContent
          this.getColumn(authorID, columnID)
        })
    },
    updateArticle () {
      const articleTitle = this.$refs.title.value
      if (!articleTitle) {
        alert('请输入博文标题')
        return
      }
      const articleContent = this.$refs.content.value
      if (!articleContent) {
        alert('请输入博文内容')
        return
      }
      const selectedIndex = this.$refs.columnList.selectedIndex
      if (selectedIndex === 0) {
        alert('请选择栏目')
        return
      }
      const columnID = this.$refs.columnList.options[selectedIndex].value
      const response = confirm('确定更新该博文吗')
      if (response) {
        const url = 'http://lipohong.fun:2333/api/articleID/' + this.$route.params.articleID
        this.$http.put(url, {columnID: columnID, articleTitle: articleTitle, articleContent: articleContent}, {'emulateJSON': true, 'credentials': true})
          .then((res) => {
            const responseData = res.body
            if (responseData.code === 1351) {
              alert('博文更新成功')
              this.common.$emit('init')
            } else if (responseData.code === 1006) {
              alert('请先登录')
              this.common.$emit('loginRequest', {})
            } else {
              alert('博文更新失败, ' + responseData.message)
            }
          })
      }
    }
  },
  mounted () {
    this.getArticle()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  header div {
    margin: 20px;
  }
  header input {
    font-size: 15px;
    width: 400px;
    height: 30px;
    border: 1px solid #bbb;
  }
  header select {
    margin-left: 20px;
    width: 200px;
    height: 35px;
    font-size: 15px;
  }
  header button {
    float: right;
  }
  .quill-box {
    height: 500px;
  }
  .quill-editor {
    margin-left: 20px;
    width: 760px;
    height: 410px;
  }
</style>



