<template>
  <div>
    <div class="router-banner">{{ message }}</div>
    <section>
      <table>
        <tr>
          <th>序号</th>
          <th>栏目名称</th>
          <th colspan="2">操作</th>
        </tr>
        <tr>
          <td>0</td>
          <td>
            <input ref="newColumn" placeholder="输入栏目名"/>
          </td>
          <td colspan="2">
            <button class="button-confirm" @click="addColumn">增加栏目</button>
          </td>
        </tr>
        <tr v-for="(list, index) in columns" v-bind:key="list.columnID">
          <td>{{ index + 1 }}</td>
          <td>
            <input :ref="list.columnID" :value="list.columnName"/>
          </td>
          <td>
            <button class="button-confirm" @click="updateColumn(list.columnID)">更改</button>
          </td>
          <td>
            <button class="button-cancel" @click="deleteColumn(list.columnID)">删除</button>
          </td>
        </tr>
      </table>
    </section>
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: '栏目管理',
      columns: []
    }
  },
  methods: {
    getColumn () {
      const url = 'http://lipohong.fun:2333/api/column/authorID/' + this.$route.params.authorID
      this.$http.get(url, {})
        .then((res) => {
          this.columns = res.body.data.columns
        })
    },
    updateColumn (columnID) {
      const response = confirm('确定更新栏目吗')
      if (response) {
        const url = 'http://lipohong.fun:2333/api/columnID/' + columnID
        // 更新栏目名
        const columnName = this.$refs[columnID.toString()][0].value
        this.$http.put(url, {columnName: columnName}, {'emulateJSON': true, 'credentials': true})
          .then((res) => {
            const responseData = res.body
            if (responseData.code === 1241) {
              alert('栏目名更新成功')
              this.common.$emit('update') // 触发首页更新信息事件
            } else if (responseData.code === 1006) {
              alert('请先登录')
              this.common.$emit('loginRequest', {})
            } else {
              alert('栏目名更新失败')
            }
            this.getColumn()
          })
      }
    },
    addColumn () {
      const columnName = this.$refs.newColumn.value
      if (!columnName) {
        alert('请输入栏目名称')
        return
      }
      const response = confirm('确定添加栏目 ' + columnName + ' 吗')
      if (response) {
        const url = 'http://lipohong.fun:2333/api/column'
        this.$http.post(url, {columnName: columnName}, {'emulateJSON': true, 'credentials': true})
          .then((res) => {
            const responseData = res.body
            if (responseData.code === 1221) {
              alert('栏目添加成功')
              this.$refs.newColumn.value = ''
              this.common.$emit('update') // 触发首页更新信息事件
            } else if (responseData.code === 1006) {
              alert('请先登录')
              this.common.$emit('loginRequest', {})
            } else {
              alert('栏目添加失败, ' + responseData.message)
            }
            this.getColumn()
          })
      }
    },
    deleteColumn (columnID) {
      const response = confirm('确定删除该栏目吗')
      if (response) {
        const url = 'http://lipohong.fun:2333/api/columnID/' + columnID
        this.$http.delete(url, {'credentials': true})
          .then((res) => {
            const responseData = res.body
            if (responseData.code === 1231) {
              alert('栏目删除成功')
              this.common.$emit('update') // 触发首页更新信息事件
            } else if (responseData.code === 1006) {
              alert('请先登录')
              this.common.$emit('loginRequest', {})
            } else {
              alert('栏目删除失败, ' + responseData.message)
            }
            this.getColumn()
          })
      }
    }
  },
  mounted () {
    this.getColumn()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  table {
    position: relative;
    width: 500px;
    left: 150px;
    margin: 30px 0;
    border: 1px solid #CCC;
    text-align: center;
  }
  table input {
    width: 200px;
    height: 25px;
    font-size: 16px;
  }
  th, td{
    border: 1px solid #CCC;
  }
</style>



